
@media screen and(min-width:320px) {

    .mainAbout {

        width: 100%;
        padding: 1rem;

        .inner_about {
            display: flex;
            flex-direction: column;
            justify-content: space-between;

            .aboutImage {
                display: flex;
                justify-content: center;
                padding: 0.5rem;
                width: 100%;

                .photoContainer {
                    width: 100%;
                    height: 65vh;
                    display: flex;
                    justify-content: space-evenly;


                    .boxOne {
                        width: 20%;
                        position: relative;

                        .imgOne {
                            position: absolute;
                            top: 2vh;
                            width: 165px;
                            z-index: 1;
                        }

                        .drawOne {
                            position: absolute;
                            top: 25vh;
                            width: 165px;
                            z-index: 1;

                        }


                    }

                    .boxTwo {
                        position: relative;
                        width: 20%;


                        .imgTwo {
                            position: absolute;
                            width: 160px;
                            bottom: 6.2vh;
                            right: 1px;
                            z-index: -1
                        }


                        .drawTwo {
                            position: absolute;
                            width: 150px;
                            top: 10vh;
                            right: 1px;
                            z-index: -1
                        }

                    }




                }
            }

            .aboutText {

                .aboutTextContent {

                    h2 {
                        font-size: 0.9rem;
                        margin-top: 0.5rem;
                    }

                    p {
                        font-size: 0.9rem;
                        margin-top: 0.5rem;
                        margin-bottom: 0.7rem;
                    }

                }

                .about_skills {
                    width: 100%;


                    .about_icon {
                        margin-top: 1rem;
                        display: flex;
                        flex-wrap: wrap;
                        justify-content: left;


                        .icon_box {
                            display: flex;
                            flex-direction: column;
                            align-items: center;
                            padding: 0.3rem;
                            margin: 0rem 0.2rem;

                            .icon {

                                font-size: 2rem;

                            }

                            p {
                                font-size: 0.6rem;
                            }
                        }


                    }

                }

                .about_contact {
                    h2 {
                        margin-bottom: 0.5rem;
                    }

                    ;

                    .contact {
                        list-style-type: none;
                        color: rgb(68, 68, 68);
                        position: relative;
                        margin-top: 0.7rem;
                        margin-bottom: 0.5rem;
                        font-size: 1rem;
                        font-weight: 500;
                        cursor: pointer;
                        text-decoration: none;
                    }

                    .contact:after {
                        content: '';
                        position: absolute;
                        top: 1.6rem;
                        width: 160px;
                        transform: scaleX(0);
                        height: 1px;
                        bottom: 0;
                        left: 0;
                        background-color: rgb(44, 44, 44);
                        transform-origin: bottom right;
                        transition: transform 0.25s ease-out;
                    }

                    .contact:hover:after {
                        transform: scaleX(1);
                        transform-origin: bottom left;

                    }

                }

                .about_links {
                    display: flex;
                    align-items: baseline;
                    justify-content: space-between;
                    margin-top: 1.5rem;


                    .about_link {
                        display: flex;
                        flex-direction: column;
                        align-items: center;
                        justify-content: center;
                        margin-left: 0.5rem;
                        margin-right: 1.5rem;
                        text-align: center;

                        a {
                            text-decoration: none;
                            font-size: 2rem;
                            margin: 0.5rem 0rem;
                            color: hotpink;
                            cursor: pointer;

                        }

                        p {
                            font-size: 0.7rem;
                        }
                    }


                }


            }


        }
    }

}


